@import url(https://fonts.googleapis.com/css2?family=Lato&family=Quicksand&display=swap);
h1,h2,h3,h4,h5,h6{
    font-family: 'Quicksand', sans-serif;
}

table .buttonsMobile{
    display: none;
}

table .buttonsDesktop{
    display: block;
}

@media(max-width:768px){
    table .buttonsDesktop{
        display: none;
    }
    table .buttonsMobile{
        display: block;
    }
}
.myForm {
    min-width: 500px;
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.Modal{
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0px;
    display: flex;
    flex-direction: column;
    align-items:center;
    background-color: rgba(7, 7, 7, 0.596);
    z-index: 2000;
    transition: all 1s ease;
    padding: 50px 0px 0px 0px;
}

.Modal .modal-dialog{
    width: 80%;
    position: relative;
    top:-1000px;
    transition: all .7s ease;
    height: auto!important;
    background-color: white;
    padding: 10px 16px 10px 16px;
    border-radius: 13px;
}

.Modal .modal-content{
    border:none!important;
}

.Modal .modal-dialog.show{
    top: 0px;
}

.Modal .modal-dialog i{
    position: absolute;
    top: -25px;
    background-color: #FFB347;
    right: -22px;
    padding: 10px;
    color: white;
    border-radius: 11px;
}

.Modal .modal-dialog{
    max-width: 55%!important;
}

@media(max-width:768px){
    .Modal .modal-dialog{
        max-width: 100%!important;
        height: 85vh!important;
    }
    .Modal .modal-dialog .modal-content{
        height: 100%;
    }
}
.detalleVenta ul{
    border-bottom:solid 3px #FFB347;
    list-style: none;
}

.datosComprador{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.datosComprador p{
    margin: 0;
    font-size: 23px;
    font-family: 'Quicksand', sans-serif;
}

.detalleVenta .datosComprador .fotoUsuario{
    height: 50px;
    border-radius: 25px;
}

.detalleVenta span{font-weight: bold;}

@media(max-width:768px){
    .detalleVenta h3{font-size: 20px;}
    .detalleVenta ul{
        font-size: 13px;
        padding-left: 0px!important;
    }

    .detalleVenta table{font-size: 13px;}
}
@media(max-width:768px){
    #formFiltroEnvios section{
        margin:15px 0px
    }
}
